  @import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');

html{
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    font-size: 10px;
}

*,*:before, *:after{
    box-sizing: inherit;
    font-family: inherit;
}

body {
    margin: 0;
}


